<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <div class="">
      <ul class="columns" v-if="posts && posts.length">
    <li class="column" v-for="post of posts"  :key="post.id">
       <div class="card">
          <div class="card-image">
            <figure class="image is-4by3"  v-if="post._embedded['wp:featuredmedia']">
                  <img :src="post._embedded['wp:featuredmedia'][0].source_url" />
            </figure>
         </div>
       </div>
       <div class="card-content">
          <div class="content">
            <h3 v-html="post.title.rendered"></h3>
            <p>{{post.body}}</p>
         </div>
      </div>
    </li>
  </ul>
  </div>

  <ul v-if="errors && errors.length">
    <li v-for="error of errors">
      {{error.message}}
    </li>
  </ul>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Portfolio',
  data () {
    return {
      msg: 'Some of my work',
      posts: [],
      errors: []
    }
  }
    // Fetches posts when the component is created.
  , created() {
    axios.get('http://35.226.129.241/wp-json/wp/v2/portfolio?_embed')
    .then(response => {
      // JSON responses are automatically parsed.
      this.posts = response.data
    })
    .catch(e => {
      this.errors.push(e)
    })

    // async / await version (created() becomes async created())
    //
    // try {
    //   const response = await axios.get(`http://jsonplaceholder.typicode.com/posts`)
    //   this.posts = response.data
    // } catch (e) {
    //   this.errors.push(e)
    // }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



